<template>
  <div class="intro">
    <div class="imgframe">
      <img src="../../img/lonemeal.jpg" width="600" height="400" />
      <p class="pb">
        The worlds of food and social interaction are inextricably linked. Food
        is often shared and acts as a means of communicating with others:
        sharing meals, cooking for loved ones, or feasting together during
        festivals. Furthermore, isolation, limited access to food, and changes
        in regular daily routines can all pose nutritional challenges.
      </p>
    </div>
    <div class="title">
      <div class="line"></div>
      <div>Healthy Meal</div>
      <div class="line"></div>
    </div>
    <section class="relate">
      <!-- gender and age form -->
      <div class="form">
        <el-form
          :model="numberValidateForm"
          ref="numberValidateForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="Gender" required>
            <el-radio-group v-model="gender" size="medium">
              <el-radio border label="Female"></el-radio>
              <el-radio border label="Male"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item
            label="Age"
            prop="age"
            :rules="[
              { required: true, message: 'Age is required' },
              { type: 'number', message: 'Number Please' },
            ]"
          >
            <el-input
              type="age"
              v-model.number="numberValidateForm.age"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button
              class="submit"
              type="primary"
              @click="submitForm('numberValidateForm')"
              >submit</el-button
            >
          </el-form-item>
        </el-form>
      </div>
      <div class="cross">
        <!-- food form -->
        <el-transfer
          @change="listchange"
          :titles="['Source', 'Target']"
          filterable
          :filter-method="filterMethod"
          filter-placeholder="Search..."
          v-model="value"
          :data="data"
        >
        </el-transfer>
      </div>
    </section>
    <!-- table shows the result -->
    <section class="foodtable">
      <el-table :data="result" style="width: 100%" height="350" empty-text="Please select food">
        <el-table-column type="selection" width="55">
        </el-table-column>
        <el-table-column fixed prop="Food Name" label="Food" width="250">
        </el-table-column>
        <el-table-column fixed prop="Protein in g" label="Protein(g)" width="150">
        </el-table-column>
        <el-table-column prop="Vitamin A retinol equivalents in ug" label="Vitamin A(ug)" width="120">
        </el-table-column>
        <el-table-column prop="Vitamin C in mg" label="Vitamin C(mg)" width="120">
        </el-table-column>
        <el-table-column prop="Calcium (Ca) in mg" label="Calcium(mg)" width="120">
        </el-table-column>
        <el-table-column prop="Total Fat in g" label="Fat(g)" width="120">
        </el-table-column>
        <el-table-column prop="Total sugars in g" label="Sugars(g)" width="120"> </el-table-column>
      </el-table>
    </section>
  </div>
</template>

<script>
import { getmeal, getTarget } from "@/network/api.js";

