        "Melon",
        "Mushroom",
        "Olive",
        "Parsley",
        "Pumpkin",
        "Radish",
        "Spinach",
        "Tomato",
        "Watercress",
        "cucumber",
        "Avocado",
        "lemon",
        "Melon",
        "e.g.",
        "e.g.",
      ];
      const pinyin = [
        "Curry",
        "Ginger",
        "apple",
        "Wine",
        "Cocoa",
        "Beverage",
        "Coffee",
        "Cordial",
        "Fruit drink",
        "Juice",
        "Soft drink",
        "Tea",
        "Water",
        "Biscuit",
        "Cone",
        "Breadcrumbs",
        "Bread",
        "Muffin",
        "Breakfast cereal",
        "Cake",
        "Doughnut",
        "Scone",
        "Porridge",
        "Amaranth",
        "Barley",
        "Millet",
        "Oats",
        "Wheat",
        "Rice",
        "Flour",
        "Pizza",
        "Noodle",
        "Pasta",
        "Lasagne",
        "Macaroni",
        "Spaghetti",
        "Croissant",
        "Pastry",
        "Sausage",
        "Pie",
        "Spring roll",
        "butter",
        "Cheese",
        "Milk",
        "Yoghurt",
        "Egg",
        "Oil",
        "Apricot",
        "Banana",
