        "Quail",
        "Buffalo",
        "Crocodile",
        "Kangaroo",
        "Ostrich",
        "Rabbit",
        "Bacon",
        "Ham",
        "Coconut",
        "Nut",
        "fish",
        "Gemfish",
        "Kingfish",
        "Milkfish",
        "Salmon",
        "Shark",
        "tuna",
        "Trout",
        "Potato",
        "Corn",
        "Corn chips",
        "Soup",
        "Chocolate",
        "Jam",
        "Honey",
        "Broccoli",
        "Cabbage",
        "Capsicum",
        "Carrot",
        "Cassava",
        "Cauliflower",
        "Chives",
        "Fennel",
        "Gherkin",
        "Leek",
        "Lettuce",
        "Melon",
        "Mushroom",
        "Olive",
        "Parsley",
        "Pumpkin",
        "Radish",
        "Spinach",
        "Tomato",
        "Watercress",
        "cucumber",
        "Avocado",
        "lemon",
        "Melon",
        "e.g.",
        "e.g.",
      ];
      cs.forEach((item, index) => {
        data.push({
          label: item,
          key: index,
          pinyin: pinyin[index],
          disabled: item == "e.g.",
        });
      });
      return data;
    };
    return {
      food: "",
      data: generateData(),
      value: [133],
      filterMethod(query, item) {
        return item.pinyin.indexOf(query) > -1;
      },
      gender: "Female",
      numberValidateForm: {
        age: "",
      },
      result: [],
      target: [],
    };
  },
  methods: {
    // send ajax request to get meal
    async send() {
      let result = await getmeal(
        this.food
      );
      this.result = result.data || result;
      // flattening array
      this.result = this.result.flat()
      console.log(result.data);
      // get Recommended Daily Nutrient Allowances
      this.target = await getTarget(this.numberValidateForm.age, this.gender)
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.numberValidateForm.age < 60) {
            this.$message.error("Age must be over 59");
            return;
          }
          // get the food name based on the index
          let len = this.value.length;
          if (len == 1) {
            this.$message.error("Must select some foods");
            return;
          }
          for (let i = 1; i < len; i++) {
            this.food += this.data[this.value[i]].label + ",";
          }
          this.food = this.food.substring(0, this.food.length - 1);
          // console.log(this.food)
          this.send();
        } else {
          this.$message.error("error submit");
          return false;
        }
      });
    },
    listchange(v, dir, mk) {
      if (dir == "left") {
        this.open2();
      } else {
        this.open1();
      }
    },
    // add success notify
    open1() {
      this.$notify({
        title: "Added",
        message: `successfully added`,
        type: "success",
      });
    },
    // remove notify
    open2() {
      this.$notify({
        title: "Removed",
        message: `successfully removed`,
        type: "warning",
      });
    },
  },
};
</script>
<style scoped>
.intro {
  position: relative;
  max-width: 1300px;
  min-width: 1000px;
  width: 100%;
  height: 100%;
  left: 50%;
  top: 0;
  transform: translate(-50%);
  overflow: hidden;
}
.title {
  position: relative;
  width: 500px;
  left: 50%;
  top: 30px;
  transform: translate(-50%);
  display: flex;
  align-items: center;
  font-size: 30px;
  text-align: center;
  margin-top: 20px;
  font-family: "Comic Sans MS", "Courier New", Courier, monospace;
}
.title .line {
  width: 95px;
  height: 2px;
  background-color: #cb980f;
  margin: auto auto;
}
.imgframe {
  position: relative;
  width: 90%;
  height: 400px;
  left: 50%;
  transform: translate(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  /* margin: 10px auto; */
}
.imgframe img {
  flex: 1;
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.5);
}
.relate {
  display: flex;
  justify-content: center;
  width: 90%;
  height: 400px;
  position: relative;
  left: 50%;
  top: 10px;
  transform: translate(-50%);
  margin-top: 60px;
}
.imgframe p {
  flex: 1;
  margin: auto 20px;
  font-family: Arial, Helvetica, sans-serif;
}
.form {
  flex: 1;
  /* width: 540px; */
  height: 100%;
  /* background-color: burlywood; */
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.pb {
  padding: 30px;
  border-radius: 15px;
  border: 1px solid #6c25a9;
}
.submit {
  width: 225px;
}
.cross {
  flex: 2;
  /* margin-left: 30px; */
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.foodtable {
  position: relative;
  width: 90%;
  height: 350px;
  left: 50%;
  transform: translate(-50%);
}
</style>